@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.todo {
  background-color: #ffffff;
  padding: 24px 35px;
  margin-bottom: 13px;
  border-radius: 5px;
  list-style-type: none;
}
.completion-checkbox {
  margin-right: 35px;
}
.delete-task {
  float: right;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}
.delete-task img {
  width: 25px;
}

.new-entry {
  background-color: #ffffff;
  margin-top: 35px;
}
.new-entry::after {
  clear: both;
  display: table;
  content: "";
}
.task-field {
  float: left;
  border: none;
  outline: none;
  padding: 18px 20px;
  width: calc(60%);
}
.deadline-input {
  float: left;
  margin-top: 10px;
}
.add-btn {
  margin-top: 8px;
  float: right;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}
.add-btn img {
  width: 35px;
}

.search-bar {
  background-color: #e4e3e3;
  border-radius: 25px;
  padding: 15px 20px;
}
.search-task {
  border: none;
  outline: none;
  width: calc(100% - 100px);
  background: none;
  margin-left: 15px;
}
.search-icon {
  width: 25px;
  float: left;
}

body {
  background-color: #f7f7f7;
}
.header {
  background-color: #686262;
  text-align: center;
  padding: 5px 0;
  color: #ffffff;
}
.completion-counter {
  color: #6b6464;
}
.app-container {
  max-width: 1100px;
  margin: 3vh auto;
}
.list-container {
  padding-left: 0;
}

.empty-list {
  width: 100%;
  height: 30vh;
  text-align: center;
  position: relative;
  background-color: #ffffff;
}
.notifiers-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.empty-info {
  color: #afa9a9;
}
@media only screen and (max-width: 1100px) {
  .app-container {
    max-width: 90%;
  }
}

