.todo {
  background-color: #ffffff;
  padding: 24px 35px;
  margin-bottom: 13px;
  border-radius: 5px;
  list-style-type: none;
}
.completion-checkbox {
  margin-right: 35px;
}
.delete-task {
  float: right;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}
.delete-task img {
  width: 25px;
}
